.mint-main {
  background-image: url(/static/media/banner_presale.d43c95c9.jpg);
  background-position: center;
  background-size: cover;
  padding: 0px 8% 50px 8%;
}

/******* Header ******/

.mint-welcome {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  font-size: 36px;
}

.mint-welcome h1 {
  font-size: 72px;
  padding-top: 16px;
}

.mint-separator hr {
  background-color: lightgray;
  /*margin-bottom: 32px;*/
}

/******* Content ******/

.mint-content-container {
  width: 100%;
  display: flex;
  margin-top: 32px;
  margin-bottom: 32px;
}

.mint-content {
  border: 2px groove lightgray;
  padding: 10px 2% 10px 2%;
  flex: 1 1;
  margin: 6px;
}

/******* Left Container ******/

.mint-content-left {
  background-color: var(--wolves-blue-dk);
  text-align: center;
  opacity: 0.75;
  color: white;
  font-size: 34px;
}

/******* Right Container ******/

.mint-content-right {
  color: var(--wolves-blue-dk);
  font-size: 24px;
}

.mint-content-right h1 {
  font-size: 60px;
  margin-bottom: 12px;
}

.mint-content-right h2 {
  font-size: 48px;
  margin-bottom: 24px;
}

.sft-description {
  margin-bottom: 16px;
}

.sft-info {
  margin-bottom: 16px;
}

.mint-content-right table {
  font-size: 18px;
  text-align: left;
  margin-top: 32px;
  margin-bottom: 16px;
  margin-left: 16px;
}

.mint-content-right td:nth-child(1) {
  background-image: url(/static/media/circle_orange.2d46f89b.svg);
  background-repeat: no-repeat;
  background-size: 8px;
  background-position-y: 10px;
  padding-right: 20px;
}

@media all and (max-width: 850px) {
  .mint-content-container {
    flex-direction: column;
  }
  .mint-content {
    margin-bottom: 2%;
  }
}

@media screen and (max-width: 400px) {
  .mint-content-right {
    font-size: 30px;
  }
}

/******* Lower button ******/

.mint-form {
  text-align: center;
  font-size: 48px;
  padding: 3px 1px;
}

.mint-btn {
  border: black 1px solid;
  background-color: var(--wolves-orange);
  height: 80px;
  width: 300px;
}

.mint-btn:disabled {
  color: var(--wolves-orange);
  background-color: var(--wolves-blue-dk);
  opacity: 0.5;
}

